// reactstrap components
import {
  Button,
  Card,
  CardHeader,
  CardBody,
  FormGroup,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Row,
  Col,
} from "reactstrap";
import { Link } from "react-router-dom";
// reactstrap components
import { NavLink } from "reactstrap";

import axios from "axios";
import { browserHistory, Redirect } from "react-router";

const Login = () => {
  return (
    <>
      {localStorage.getItem("userInfo") && (
        <Redirect to="/admin/index"></Redirect>
      )}
      <Col lg="5" md="7">
        <Card className="bg-secondary shadow border-0">
          <CardBody className="px-lg-5 py-lg-5">
            <div className="text-center text-muted mb-4">
              <small>Sign in with credentials</small>
            </div>
            <Form role="form" onSubmit={(e) => login(e)}>
              <FormGroup className="mb-3">
                <InputGroup className="input-group-alternative">
                  <InputGroupAddon addonType="prepend">
                    <InputGroupText>
                      <i className="ni ni-email-83" />
                    </InputGroupText>
                  </InputGroupAddon>
                  <Input
                    placeholder="Email"
                    type="email"
                    id="email"
                    autoComplete="new-email"
                  />
                </InputGroup>
              </FormGroup>
              <FormGroup>
                <InputGroup className="input-group-alternative">
                  <InputGroupAddon addonType="prepend">
                    <InputGroupText>
                      <i className="ni ni-lock-circle-open" />
                    </InputGroupText>
                  </InputGroupAddon>
                  <Input
                    placeholder="Password"
                    type="password"
                    id="password"
                    autoComplete="new-password"
                  />
                </InputGroup>
              </FormGroup>
              <div className="custom-control custom-control-alternative custom-checkbox">
                <input
                  className="custom-control-input"
                  id=" customCheckLogin"
                  type="checkbox"
                />
                <label
                  className="custom-control-label"
                  htmlFor=" customCheckLogin"
                >
                  <span className="text-muted">Remember me</span>
                </label>
              </div>
              <div className="text-center">
                <Button className="my-4" color="primary" type="submit">
                  Sign in
                </Button>
              </div>
            </Form>
          </CardBody>
        </Card>
        <Row className="mt-3">
          <Col xs="6">
            <a
              className="text-light"
              href="#pablo"
              onClick={(e) => e.preventDefault()}
            >
              <small>Forgot password?</small>
            </a>
          </Col>
          <Col className="text-right" xs="6">
            <a
              className="text-light"
              href="#pablo"
              onClick={(e) => e.preventDefault()}
            >
              <NavLink
                className="nav-link-icon text-light"
                to="/auth/register"
                tag={Link}
              >
                <span className="nav-link-inner--text">Create Account</span>
              </NavLink>
            </a>
          </Col>
        </Row>
      </Col>
    </>
  );
};

function login(e) {
  e.preventDefault();
  const request = {
    email: document.getElementById("email").value,
    password: document.getElementById("password").value,
  };
  console.log(request);
  axios
    .post("http://localhost:5000/api/auth/login", request)
    .then((res) => {
      if (res.data.message == "Sucessfully Logged In") {
        localStorage.setItem("userInfo", res.data.token);
        window.location.replace("/admin/index");
      } else {
        alert(res.data.message);
      }
    })
    .catch((error) => {
      console.log(error);
    });
  e.target.reset();
}

export default Login;
